.home-page__nav {
  margin: 150px 0 0;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  a {
    font-weight: bold;
    display: block;
    color: #fff;
    text-decoration: none;
    text-transform: uppercase;
    margin: 0 15px;
  }
}

.modal-btn {
  cursor: pointer;
}
