.form {
  padding: 0 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: inherit;
  input, textarea {
    max-width: 100%;
    appearance: none;
    outline: 0;
    border: 1px solid rgba(255, 255, 255, 0.4);
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 3px;
    padding: 5px 15px;
    margin: 0 auto 10px auto;
    display: block;
    text-align: center;
    font-size: 18px;
    color: white;
    transition-duration: 0.25s;
    font-weight: 300;
  }

  textarea {
    padding: 10px;
    height: 120px;
    resize: none;
  }

  .btn {
    margin: 0 auto;
    padding: 5px 10px;
    max-width: 100px;
    width: 100%;
    display: block;
    border: none;
    border-radius: 4px;
    background-color: #e0e0e0;
    transition: background-color .3s ease;
    text-transform: uppercase;
    font-weight: bold;
    color: #222;
    cursor: pointer;
    outline: none;
    &:hover {
      background-color: #fff;
    }
  }

  &-group {
    position: relative;
  }
}

.invalid-message {
  display: block;
  position: absolute;
  bottom: -10px;
  color: #b71c1c;
  font-size: 12px;
  opacity: 0;
  transition: all .3s ease;
}

.validated {
  input:invalid,
  textarea:invalid {
    border: 1px solid #e64943;
    + .invalid-message { opacity: 1; }
  }
}
